  justify-content: center;
  width: 79%;
  gap: 1vw 3%;
}
@media (max-width: 1199px) {
  .connection form {
    align-self: stretch;
    margin: 0 auto;
    flex-direction: column;
    width: 35%;
    gap: 0.9vw 0;
  }
}
@media (max-width: 767px) {
  .connection form {
    margin: 0;
    width: 100%;
    gap: 1.8vw 0;
  }
}

.connection__submit {
  color: #ffffff;
  background-color: #0266C1;
  display: flex;
  justify-content: center;
  padding: 2% 0 1.8%;
  min-width: 20.9%;
  outline: none;
  box-shadow: none;
  border: 1px solid #0266C1;
}
@media (max-width: 1199px) {
  .connection__submit {
    width: 100%;
    font-size: 2.1vw;
    margin: 1vw auto 1vw;
    padding: 2.3vw 2.09vw 2.3vw;
  }
}
@media (max-width: 767px) {
  .connection__submit {
    font-size: 5vw;
    padding: 16px 20px 14px;
  }
}

.contacts {
  position: relative;
}
.contacts > .container {
  padding: 6% 8.3333333333% 4.3055555556%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  flex-wrap: wrap;
}
@media (max-width: 1199px) {
  .contacts > .container {
    grid-template-columns: 1fr;
    padding: 12.4% 6% 5.6%;
  }
}
@media (max-width: 767px) {
  .contacts > .container {
    padding: 20% 5% 12.4%;
  }
}

.contacts__info {
  font-style: normal;
}

.contacts__title {
  margin-bottom: 4%;
}
@media (max-width: 1199px) {
  .contacts__title {
    grid-row: 1/2;
    margin-bottom: 2.8%;
  }
}

.contacts__text {
  width: 86%;
  letter-spacing: -0.1px;
  margin: 0 0 7.3%;
}
@media (max-width: 1199px) {
  .contacts__text {
    grid-row: 2/3;
    margin: 0 0 5.3%;
  }
}
@media (max-width: 767px) {
  .contacts__text {
    width: 100%;
    font-size: 4.39vw;
    letter-spacing: -0.13px;
    margin: 0 0 9.3%;
  }
}

.contacts__item {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 4.6%;
  flex-direction: row;
  align-items: center;
  font-size: 1.3vw;
  gap: 2vw;
}
@media (max-width: 1199px) {
  .contacts__item {
    display: inline-flex;
    grid-row: 3/4;
    font-size: 1.83vw;
    gap: 1.4vw;
    width: 27%;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    width: 80%;
    display: flex;
    gap: 3vw;
    font-size: 4.39vw;
    margin-bottom: 8.6%;
  }
}
@media (max-width: 1199px) {
  .contacts__item:last-of-type {
    width: 44.5%;
    gap: 0.6vw;
  }
}
@media (max-width: 767px) {
  .contacts__item:last-of-type {
    width: 100%;
    gap: 3vw;
  }
}
.contacts__item:last-of-type p {
  width: 58%;
}
@media (max-width: 1199px) {
  .contacts__item:last-of-type p {
    width: 86%;
  }
}
@media (max-width: 767px) {
  .contacts__item:last-of-type span {
    width: 67%;
  }
}
@media (max-width: 1199px) {
  .contacts__item:last-of-type svg {
    margin-left: 5%;
  }
}
@media (max-width: 767px) {
  .contacts__item:last-of-type svg {
    margin: 1% 0 0;
  }
}
.contacts__item p {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 0;
}
.contacts__item b {
  font-weight: 600;
}
.contacts__item svg {
  scale: 1.25;
}
@media (max-width: 1199px) {
  .contacts__item svg {
    scale: 1.1;
    align-self: start;
    margin-top: 2%;
  }
}
@media (max-width: 767px) {
  .contacts__item svg {
    margin-top: 1%;
    scale: 1;
  }
}
.contacts__item span,
.contacts__item a {
  font-size: 1.12vw;
  line-height: 1.8;
  letter-spacing: -0.06px;
}
@media (max-width: 1199px) {
  .contacts__item span,
.contacts__item a {
    font-size: 1.8vw;
  }
}
@media (max-width: 767px) {
  .contacts__item span,
.contacts__item a {
    font-size: 4.3vw;
  }
}

.contacts__location {
  width: 100%;
  height: 22.6vw;
  margin-bottom: 2vw;
  align-self: flex-end;
}
@media (max-width: 1199px) {
  .contacts__location {
    width: 100%;
    height: 45vw;
  }
}
@media (max-width: 767px) {
  .contacts__location {
    height: 100vw;
  }
}

.action {
  transition: 300ms;
}
.action:focus, .action:hover {
  color: #1C3374;
  background-color: #ffffff;
  border-color: #1C3374;
}
.action:active {
  background-color: #1C3374;
  border-color: #1C3374;
}
.action:focus-visible {
  color: #1C3374;
  background-color: #ffffff;
  border-color: #1C3374;
}
.action:disabled {
  background-color: #0266C1;
  border-color: #0266C1;
  opacity: 0.1;
}

.btn {
  display: inline-flex;
  color: #ffffff;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.buttons {
  display: flex;
  gap: 0 0.3vw;
}
@media (max-width: 1199px) {
  .buttons {
    gap: 0 0.5vw;
  }
}
@media (max-width: 767px) {
  .buttons {
    gap: 0 1vw;
  }
}

.button {
  position: relative;
  display: block;
  width: 2.55vw;
  height: 2.5vw;
  color: #ffffff;
  background-color: #0266C1;
  border: 1px solid rgba(255, 255, 255, 0);
  outline: none;
  cursor: pointer;
  transition: all 0.3s ease;
}
@media (max-width: 1199px) {
  .button {
    width: 4.85vw;
    height: 4.7vw;
  }
}
@media (max-width: 767px) {
  .button {
    width: 12vw;
    height: 11vw;
  }
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    color: #1C3374;
    background-color: #ffffff;
    border-color: #1C3374;
  }
}
.button.focus-visible:focus {
  color: #1C3374;
  background-color: #ffffff;
  border-color: #1C3374;
}
.button.focus-visible:active {
  color: #ffffff;
  background-color: #1C3374;
}
.button:disabled {
  color: #ffffff;
  background-color: rgba(2, 102, 193, 0.2);
  pointer-events: none;
}

.button svg {
  position: absolute;
  margin: auto;
  inset: 0;
}

.button.is-prev svg {
  rotate: 180deg;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.custom-input__label {
  position: fixed;
  transform: scale(0);
}

.custom-input input {
  width: 340px;
  padding: 23px 30px 22px;
  font-size: 16px;
  line-height: 16px;
  background-color: #ffffff;
  border: none;
  outline: none;
  box-shadow: inset 0 0 0 1px rgba(2, 102, 193, 0.1);
  cursor: text;
  transition: box-shadow 0.3s ease;
}
.custom-input input::placeholder {
  color: rgba(45, 56, 63, 0.5);
}
.custom-input input:focus::-webkit-input-placeholder, .custom-input input:active::-webkit-input-placeholder {
  color: transparent;
}
@media (hover: hover) {
  .custom-input input:hover:not(.focus-visible) {
    box-shadow: inset 0 0 0 1px #0266C1;
  }
  .custom-input input:hover:not(.focus-visible)::placeholder {
    color: #0266C1;
    opacity: 1;
    transition: color 0.3s ease;
  }
}
.custom-input input:active {
  box-shadow: inset 0 0 0 1px #1C3374;
}
.custom-input input:disabled {
  pointer-events: none;
}
.custom-input input:disabled::placeholder {
  color: rgba(45, 56, 63, 0.1);
}
@media (max-width: 1199px) {
  .custom-input input {
    width: 100%;
    padding: 16px 15px;
    font-size: 14px;
    line-height: 14px;
  }
}

.custom-input.is-invalid input {
  box-shadow: inset 0 0 0 1px #FF121F;
}

.custom-toggle {
  position: relative;
  font-size: 18px;
  line-height: 27px;
  font-weight: 300;
  display: flex;
  width: 500px;
  padding: 14px 0 10px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle {
    padding: 14px 0 10px 0;
    width: 100%;
  }
}
.custom-toggle:last-of-type {
  padding: 14px 0 58px 36px;
}
@media screen and (max-width: 767px) {
  .custom-toggle:last-of-type {
    padding: 14px 0 58px 0;
  }
}
.custom-toggle input {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}
.custom-toggle label {
  position: relative;
  padding-left: 52px;
  cursor: pointer;
}
.custom-toggle label span {
  margin-bottom: 0;
  margin-left: 0;
}
.custom-toggle--checkbox .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1.5px solid #2c39f2;
  transition: border 0.3s ease;
}
.custom-toggle--checkbox .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background-color: rgba(44, 57, 242, 0);
  transform: translate(-50%, -50%);
  transition: background-color 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon {
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 28px;
  border: 1px solid #2c39f2;
  border-radius: 50%;
  transition: border 0.3s ease;
}
.custom-toggle--radio .custom-toggle__icon::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  color: rgba(255, 255, 255, 0);
  border-radius: 50%;
  transform: translate(-50%, -50%);
  transition: color 0.3s ease;
}
.custom-toggle input:checked + .custom-toggle__icon {
  border: 1.5px solid #2c39f2;
}
.custom-toggle input:checked + .custom-toggle__icon::before {
  background-color: #2c39f2;
}
.custom-toggle.is-invalid label span {
  color: #ff1553;
}
.custom-toggle .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-toggle .input-message.is-invalid {
  color: #ff0000;
}
.custom-toggle .input-message.is-valid {
  color: #15ab07;
}

.custom-select {
  position: relative;
  width: 300px;
  padding: 14px 0 28px;
  margin: 0 0 44px 0;
  /* &.is-valid {
    .custom-select__button {
      border: 2px solid $color-eucalyptus;

      &:focus,
      &:hover {
        border: 2px solid rgba($color-eucalyptus, 0.5);
      }
    }
  } */
}
@media screen and (max-width: 767px) {
  .custom-select {
    padding: 4px 0 14px;
    width: 100%;
  }
}
.custom-select__label {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  .custom-select__label {
    transform: translateY(calc(-50% - 5px));
  }
}
.custom-select__placeholder {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
  pointer-events: none;
}
@media screen and (max-width: 767px) {
  .custom-select__placeholder {
    transform: translateY(calc(-50% - 5px));
  }
}
.custom-select select {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.custom-select__button {
  display: flex;
  align-items: center;
  width: 100%;
  height: 48px;
  padding: 5px 10px;
  background-color: rgba(255, 255, 255, 0);
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  outline: none;
  cursor: pointer;
  transition: border 0.3s ease;
}
.custom-select__button:hover {
  border: 2px solid rgba(0, 0, 0, 0.5);
}
.custom-select__button:focus {
  border: 2px solid rgba(0, 0, 0, 0.8);
}
.custom-select__button:disabled {
  pointer-events: none;
  opacity: 0.3;
}
.custom-select__list {
  position: absolute;
  bottom: 10px;
  z-index: 1;
  width: 100%;
  max-height: 100px;
  margin: 0;
  padding: 0;
  overflow-y: auto;
  list-style: none;
  background-color: #ffffff;
  border: 2px solid #000000;
  transform: translateY(100%);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease, opacity 0.3s ease;
}
.custom-select__item {
  padding: 5px;
  outline: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.custom-select__item[aria-selected=true] {
  background-color: rgba(0, 0, 0, 0.2);
}
@media (hover: hover) {
  .custom-select__item[aria-selected=true]:hover:not(.focus-visible) {
    background-color: rgba(0, 0, 0, 0.2);
  }
}
.custom-select__item[aria-selected=true].focus-visible:focus {
  background-color: rgba(0, 0, 0, 0.2);
}
@media (hover: hover) {
  .custom-select__item:hover:not(.focus-visible) {
    background-color: rgba(0, 0, 0, 0.4);
  }
}
.custom-select__item.focus-visible:focus {
  background-color: rgba(0, 0, 0, 0.4);
}
.custom-select__error {
  position: absolute;
  bottom: -15px;
  left: 0;
  color: #ff1553;
  opacity: 0;
  transition: opacity 0.1s ease;
}
.custom-select__text {
  font-size: 20px;
  line-height: 24px;
  flex-shrink: 0;
  width: 80%;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  text-overflow: ellipsis;
  opacity: 0;
  transition: opacity 0.3s ease 0.1s;
}
.custom-select__text br {
  display: none;
}
.custom-select__icon {
  flex-shrink: 0;
  width: 20px;
  height: 10px;
  margin-left: auto;
  background-color: #000000;
  transition: transform 0.3s ease;
  clip-path: polygon(100% 0, 100% 20%, 50% 100%, 0 20%, 0 0, 50% 80%);
}
.custom-select.is-disabled {
  pointer-events: none;
}
.custom-select.is-disabled .custom-select__list {
  display: none;
}
.custom-select.open-up .custom-select__list {
  bottom: auto;
  top: 0;
  transform: translateY(calc(-100% + 10px));
}
.custom-select.is-open .custom-select__list {
  visibility: visible;
  opacity: 1;
}
.custom-select.is-open .custom-select__icon {
  transform: rotate(180deg);
}
.custom-select.is-open .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__text {
  opacity: 1;
}
.custom-select.not-empty .custom-select__label {
  opacity: 0;
}
.custom-select.not-empty .custom-select__placeholder {
  opacity: 0;
}
.custom-select.is-invalid .custom-select__error {
  opacity: 1;
}
.custom-select.is-invalid .custom-select__button {
  border: 2px solid #ff1553;
}
.custom-select.is-invalid .custom-select__button:focus, .custom-select.is-invalid .custom-select__button:hover {
  border: 2px solid rgba(255, 21, 83, 0.5);
}
.custom-select--placeholder .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select--placeholder.is-open .custom-select__label {
  opacity: 1;
}
.custom-select--placeholder.not-empty .custom-select__label {
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__label, .custom-select.slide-up.is-open .custom-select__label {
  transform: translateY(calc(-50% - 40px));
  opacity: 1;
}
.custom-select.slide-up.not-empty .custom-select__text, .custom-select.slide-up.is-open .custom-select__text {
  opacity: 1;
}
.custom-select .input-message {
  position: absolute;
  right: 0;
  bottom: -5px;
}
.custom-select .input-message.is-invalid {
  color: #ff0000;
}
.custom-select .input-message.is-valid {
  color: #15ab07;
}

